*{
  margin: 10px;
}

body {
  font-family: Arial, sans-serif;
  background-color: #a4d7f5;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  height: 100%;
  max-height: 700px;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.add_product_section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.add_input {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #4e4e4e;
  border-radius: 5px;
  margin-right: 10px;
}

.add_but {
  background-color: #3e49fb;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  justify-items: center;
}

.controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}


.main_product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 150px; 
  height: 100px; 
  padding: 15px;
  border: 1px solid #4e4e4e;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.main_product p {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
}

.but_count_m,
.but_count_p {
  background-color: #3e49fb;
  color: white;
  border: none;
  border-radius: 20%;
  width: 34px;
  height: 40px;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.product-count {
  font-size: 20px;
  font-weight: bold;
}

.error_message {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}
